<template>
    <div class="app_carousel">
        <el-carousel :interval="3000" arrow="always" indicator-position='none' class="carousel">
            <el-carousel-item style="position:absolute;top:0;">
                <img src="../../static/img/banner1.png" alt="">
            </el-carousel-item>
        </el-carousel>
    </div>
</template>
<script>
    export default {
        data(){
            return{
                list:[]
            }
        }
    }
</script>
<style lang="less" scoped>
    div.el-carousel__container {
        width:100%;
        height: 4.2rem;
        z-index: 1;
    }
    div.el-carousel__container img{
        width:100%;
        height: 100%;
       
    }
   
</style>





